.App {
  text-align: center;
}

.btn {
  font-size: min(max(14px, 4vw),16px);
  font-family: 'Yatra One', cursive;
  font-weight: 300;
}

h1 {
  font-size: min(max(32px, 4vw),48px);
  font-family: 'Yatra One', cursive;
  font-weight: 700;
}

h2 {
  font-size: min(max(26px, 4vw),36px);
  font-family: 'Yatra One', cursive;
  font-weight: 700;
}

h3 {
  font-size: min(max(22px, 4vw),28px);
  font-family: 'Yatra One', cursive;
  font-weight: 400;
}

h4 {
  font-size: min(max(18px, 4vw),20px);
  font-family: 'Yatra One', cursive;
  font-weight: 300;
}

.container::before  {
  display: block;
  content: " ";
  margin-top: -71px;
  height: 71px;
  visibility: hidden;
}

.jeneng {
  font-family: 'Yatra One', cursive;
}

.jeneng.navbar-brand {
